.main-page {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  color: #333;
}

.main-text {
  font-size: 7rem;
  font-weight: bold;
  margin-bottom: 2rem;
  color: tomato;
  text-shadow: -5px 5px rgb(226, 217, 86);
}

.start > p {
  text-align: center;
  margin-bottom: 0;
  font-weight: bold;
}

.start > .pang {
  display: block;
  text-align: center;
  font-size: 2.5rem;
}

.start-button {
  width: 100%;
  border: none;
  outline: none;
  background-color: tomato;
  color: white;
  font-size: 2rem;
  border-radius: 7px;
  padding: 0.5rem;
  margin-top: 1rem;
  transition: 0.2s;
}

.start-button:hover {
  background-color: rgb(226, 217, 86);
}