.lotto {

}

.result {
  width: 100%;
  height: 300px;
  box-sizing: border-box;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(3, 1fr)
}

.numbers > span,
.bonus > span {
  display: block;
  font-size: 3rem;
  font-weight: bold;
  color: tomato;
  text-align: center;
  margin-bottom: 1.5rem;
}

.numbers,
.bonus {
  margin-bottom: 1.5rem;
}

.bonus > span {
  color: darkolivegreen;
}

.bonus .ball {
  margin: 0 auto;
}

.ball {
  width: 28vw;
  height: 28vw;
  border-radius: 50%;
  margin-bottom: 1rem;
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  color: #fff;
  line-height: 110px;
}

.redo {
  width: 100%;
  border: none;
  outline: none;
  background-color: tomato;
  color: white;
  font-size: 2rem;
  border-radius: 7px;
  padding: 0.5rem;
  margin-top: 1rem;
  transition: 0.2s;
}